.fecthShowLoading {
  color: #808080;
  padding: 20px; }

.fecthShow .background-image, .fecthShow .background-shadow {
  position: absolute;
  background-size: cover;
  display: block;
  min-height: 576px;
  left: 0;
  top: 0;
  right: 0;
  background-repeat: no-repeat;
  background-size: cover; }

.fecthShow .background-image {
  z-index: 6; }

.fecthShow .background-shadow {
  z-index: 7;
  background: -webkit-radial-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.8), black);
  background: -o-radial-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.8), black);
  background: radial-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.8), black); }

.fecthShow .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 8;
  padding: 0;
  max-width: 1024px;
  min-width: 1024px; }

.fecthShow .close {
  position: fixed;
  right: 0;
  margin: 30px 35px;
  cursor: pointer; }



