.menuSeasons {
  min-height: 380px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
  justify-content: flex-end;
  padding-right: 50px;
  width: 130%; }
  .menuSeasons .main {
    min-width: 340px;
