@-webkit-keyframes slowshow {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes slowshow {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
  .menuSeasons .show {
    display: inline-block;
    width: 100%;
    -webkit-animation: slowshow 1s 1 forwards;
    animation: slowshow 1s 1 forwards; }
  .menuSeasons .episodeTitle {
