.menuBottom {
  background-color: #101010;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 10px;
  width: 125%; }
  .menuBottom .select {
    text-align: left;
    height: 45px;
    padding: 0 0 5px 0px; }
  .menuBottom .selectGeral {
    min-width: 120px; }
  .menuBottom .selectElenco {
    min-width: 135px; }
  .menuBottom .selectPremios {
    min-width: 135px; }
  .menuBottom .txt {
    position: absolute;
    width: 1px;
    padding-top: 20px;
    padding-left: 5px;
    cursor: pointer; }
  .menuBottom .txt:hover {
    font-size: 17px;
    padding-top: 18px;
    padding-left: 3px; }
  .menuBottom .selectedGeral {
    background-image: url("../images/menuselectedGeral.png");
    background-repeat: repeat-x; }
  .menuBottom .selectedElenco {
    background-image: url("../images/menuselectedElenco.png");
    background-repeat: repeat-x; }
  .menuBottom .unselected {
    background-image: url("../images/menuunselectedbottom.png");
    background-repeat: repeat-x; }
  .menuBottom .providerDiv {
    padding-left: 92%;
    height: 40px;
    padding-top: 5px; }
  .menuBottom .nav {
    padding-top: 15px;
    font-size: 12px;
    height: 150px; }



